*,body {
    box-sizing: border-box;
    font-family: 'Stem', sans-serif;
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 26px;
    font-weight: 350;
    height: auto;
    @media(max-width: 1024px) {
        font-size: 16px;
        line-height: 23px;
    }
}

body {
    &.no_scroll {
        height: 100vh;
        overflow: hidden;
    }
}
ul {
    padding-left: 0;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}


.container {
    max-width: 1800px;
    padding: 0 40px;
    margin: 0 auto;
    @include sm-block {
        padding: 0 20px;
    }
}

.btn {
    display: inline-block;
    border: none;
    outline: none;
    border-radius: 8px;
    padding: 0 20px;
    height: 80px;
    background: #03A543;
    color: #fff;
    font-size: 20px;
    line-height: 80px;
    text-align: center;
    cursor: pointer;
    &--border {
        border: 1px solid #1C1C1C;
        color: #1C1C1C;
        background-color: transparent;
    }
    &:hover {
        opacity: .7;
    }
}


.slider__next {
    width: 52px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    cursor: pointer;
    svg {
        display: block;
        transform: rotate(180deg);
    }
}
.slider__prev {
    width: 52px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    cursor: pointer;
    svg {
        display: block;
    }
}

.slider {
    overflow: hidden;
}


.radio {
    

    &__label {
        padding-left: 50px;
        position: relative;
        cursor: pointer;
        &:hover {
            opacity: .7;
        }
        &::before {
            position: absolute;
            left: 0;
            top: 0;
            content: '';
            width: 20px;
            height: 20px;
            border: 2px solid #03A543;
            border-radius: 50%;
        }
        &::after {
            position: absolute;
            left: 5px;
            top: 5px;
            content: '';
            width: 10px;
            height: 10px;
            border: 2px solid #03A543;
            background-color: #03A543;
            border-radius: 50%;
            opacity: 0;
        }
        @include sm-block {
            &::before {
                width: 16px;
                height: 16px;
            }
            &::after {
                width: 8px;
                height: 8px;
                top: 4px;
                left: 4px;
            }
        }
    }
    &__input {
        display: none;
        width: 0;
        height: 0;
        &:checked {
            + .radio__label {
                &::after {
                    opacity: 1;
                }
            }
        }
    }
}