.product-head {
    padding: 10px 0 30px;
    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__action {
        margin-left: 50px;
        display: flex;
        align-items: center;
        gap: 15px;
        button {
            background-color: #fff;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: 1px solid rgba(#1C1C1C, .2);
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            svg {
                width: 18px;
                height: 18px;
                display: block;
            }
            &:hover {
                background-color: #1C1C1C;
                svg {
                    path {
                        fill: #fff;
                    }
                }
            }
        }
    }
}

.product__articul {
    margin: 0 0 0 auto;
    font-size: 14px;
    line-height: 14px;
}

.product-main__item {
    background: url('../images/item-1.png') center center / cover no-repeat;
    min-height: 720px;
    padding: 40px 0;
    position: relative;
}

.product-main__3d {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: #03A543;
    color: #fff;
    padding: 18px 14px;
    text-align: center;
    font-size: 20px;
    line-height: 20px;
    position: absolute;
    left: 40px;
    top: 40px;
}

.product-main__title {
    font-size: 52px;
    line-height: 52px;
    font-weight: 500;
    margin-bottom: 20px;
}

.product-main__info {
    position: absolute;
    right: 40px;
    bottom: 170px;
    @media(max-width: 1600px) {
        bottom: 80px;
    }
}

.product-main__subtitle {
    font-size: 20px;
    line-height: 20px;
}

.product-about {
    padding: 200px 0;
}

.product-about__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
}

.product-about__left {
    padding: 70px 60px;
    background: #F9F9F9;
    border-radius: 16px;
}

.product-about__title {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
    font-weight: 500;
}

.product-about__desc {
    margin-bottom: 30px;
}

.product-about__action {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
}


.product-options {
    margin-bottom: 40px;
}

.product-options__title {
    font-weight: 500;
    margin-bottom: 15px;
}


.product-tech {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 60px;
}
.product-tech__item {
    img {
        width: 100%;
        display: block;
    }
}

.product-parameters {
    &__title {
        font-size: 28px;
        line-height: 28px;
        margin-bottom: 60px;
        font-weight: 500;
    }
    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 60px;
    }
    &__item {
        color: #8A8A8A;
        font-size: 20px;
        line-height: 22px;
        span {
            display: block;
            margin-bottom: 18px;
            font-weight: 500;
            font-size: 22px;
            line-height: 1;
            color: #000;
        }

    }
}


.product-video {
    background: url('../images/video-item.png') center center / cover no-repeat;
    padding: 80px 0;
    .container {
        position: relative;
        min-height: 740px;
    }
}

.product-video__title {
    position: absolute;
    top: 0;
    left: 40px;
    color: #fff;
    font-size: 48px;
    line-height: 1;
    font-weight: 500;
}

.product-video__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    span {
        display: block;
        color: #fff;
        font-size: 24px;
        line-height: 1;
        margin-top: 30px;
    }
}

.product-video__info {
    position: absolute;
    right: 40px;
    bottom: 0;
    max-width: 169px;
    text-align: right;
    color: #fff;
    opacity: 1;
    font-weight: 500;
    span {
        display: block;
        margin-bottom: 20px;
        color: #FFFFFF;
        opacity: .4;
    }
}

.product-bracing {
    padding: 180px 0 90px;
}

.product-bracing__content {
    display: flex;
    align-items: center;
    gap: 106px;
    @media(max-width: 1600px) {
        gap: 60px;
    }
}

.product-bracing__img {
    max-width: 1027px;
    width: 100%;
    overflow: hidden;
    border-radius: 15px;
    img {
        width: 100%;
        display: block;
        object-fit: cover;
    }
}

// .product-bracing__info {
//     padding-left: 106px;
// }

.product-bracing__title {
    max-width: 400px;
    font-weight: 500;
    font-size: 48px;
    line-height: 57px;
    margin-bottom: 50px;
}

.product-bracing__type {
    margin-bottom: 25px;
    font-size: 24px;
    line-height: 24px;
    padding-left: 40px;
    cursor: pointer;
    &:hover {
        opacity: .7;
    }
    &.active {
        font-weight: 500;
        background: url('../images/arrow-tab.svg') left center / 25px no-repeat;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.product-colors {
    padding: 90px 0 180px;
}

.product-colors__content {
    display: flex; 
    align-items: center;
    gap: 106px;
    @media(max-width: 1600px) {
        gap: 60px;
    }
}

.product-colors__img {
    max-width: 1027px;
    width: 100%;
  
    img {
        width: 100%;
        object-fit: cover;
        display: block;
    }
}

.product-colors__title {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 70px;
    font-weight: 500;
}

.product-colors__info {
    max-width: 587px;
    width: 100%;
}

.product-colors__panel {
    padding: 40px 50px;
    background: #F5F5F5;
    border-radius: 20px;
}

.product-colors__subtitle {
    margin-bottom: 50px;
}

.product-colors__slider {
    position: relative;
    padding: 0 86px;
    .slider__prev {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
    .slider__next {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
    }
}

.product-colors__slider-item {
    img {
        width: 100%;
        display: block;
    }
}


.product-structure {
    padding: 100px 0 50px;
    background-color: #F5F5F5;
    .container {
        display: flex;
        justify-content: center;
        position: relative;
    }
    &__title {
        position: absolute;
        top: 0;
        left: 40px;
        max-width: 320px;
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 70px;
        font-weight: 500;
    }
    &__img {
        max-width: 850px;
        width: 100%;
        margin: 0 142px 0 48px;
        position: relative;
        img {
            width: 100%;
        }
    }
    &__point {
        position: absolute;
        width: 36px;
        height: 36px;
        background: rgba(3, 165, 67, 0.5);
        backdrop-filter: blur(2.5px);
        border-radius: 50%;
        &::before {
            position: absolute;
            content: '';
            width: 6px;
            height: 6px;
            background-color: #fff;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            border-radius: 50%;
        }
        &::after {
            position: absolute;
            content: '';
            width: 82px;
            height: 82px;
            background: rgba(3, 165, 67, 0.6);
            backdrop-filter: blur(2.5px);
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%) scale(.4);
            border-radius: 50%;
            z-index: -1;
            opacity: 0;
            transition: .5s;
        }
        &:hover {
            &::after {
                opacity: 1;
                transform: translate(-50%,-50%) scale(1);
                transition: .2s;
            }
        }
        &.active {
            &::after {
                opacity: 1;
                transform: translate(-50%,-50%) scale(1);
                transition: .2s;
            }
        }
        &:nth-of-type(1) {
            top: 163px;
            left: 112px;
        }
        &:nth-of-type(2) {
            top: 332px;
            left: 112px;
        }
        &:nth-of-type(3) {
            top: 207px;
            right: 76px;
        }
        &:nth-of-type(4) {
            top: 343px;
            right: 76px;
        }
        &:nth-of-type(5) {
            top: 526px;
            right: 76px;
        }
    }
    &__item {
        max-width: 244px;
        &--one {
            margin: 219px 0 0 0;
        }
        &--two {
            margin: 82px 0 0 0;
        }
        &--three {
            margin-top: 222px;
        }
        &--four {
            margin-top: 112px;
        }
        &--five {
            margin-top: 141px;
        }
    }
}


.product-size {
    padding: 150px 0;
    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__title {
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 70px;
        font-weight: 500;
        margin-bottom: 70px;
    }
    &__img {
        max-width: 505px;
        margin-right: -40px;
        img {
            width: 100%;
            display: block;
        }
    }
    &__next {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        cursor: pointer;
        svg {
            transform: rotate(180deg);
        }
    }
    &__prev {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        cursor: pointer;
    }
    &__slider-item {
        &.swiper-slide {
            opacity: .5;
        }
        &.swiper-slide-active {
            opacity: 1;
            .product-size__slider-text {
                color:#03A543;
                font-weight: 500;
            }
        }
    }
    &__slider {
        width: 100%;
        padding: 0 48px;
        position: relative;
        .slider {
            max-width: 836px;
            width: 100%;
            position: relative;
            @media(max-width: 1200px) {
                max-width: 650px;
            } 
        }
        &-img {
            img {
                width: 100%;
                display: block;
            }
        }
        &-text {
            text-align: center;
            color: #1C1C1C;
            opacity: 0.5;
            font-size: 18px;
            line-height: 18px;
        }
    }
}

.product-light {
    img {
        width: 100%;
        display: block;
    }
}

.product-corner {
    padding: 180px 0 90px;
    @include sm-block {
        padding: 70px 0 35px;
    }
    &__content {
        display: flex;
        align-items: center;
        gap: 106px;
        @media(max-width: 1024px) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 20px;
        }
        @include sm-block {
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-start;
        }
    }
    &__img {
        max-width: 1026px;
        width: 100%;
        img {
            width: 100%;
            display: block;
        }
    }
    &__info {
        max-width: 440px;
        width: 100%;
    }
    &__title {
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 70px;
        font-weight: 500;
        margin-bottom: 50px;
        @media(max-width: 1024px) {
            font-size: 28px;
            line-height: 28px;
            margin-bottom: 40px;
        }
    }   
}

.product-options  {
    padding: 90px 0;
    @include sm-block {
        padding: 35px 0;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 40px;
        @media(max-width: 1024px) {
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px;
        }
        @include sm-block {
            grid-template-columns: 1fr;
        }
    }
    &__item {
        padding: 40px;
        background-color: #F5F5F5;
        border-radius: 8px;
    }
    &__img {
        margin-bottom: 40px;
        img {
            max-width: 100%;
            width: auto;
            display: block;
            height: 106px;
        }
    }
    &__text {
        span {
            font-weight: 500;
        }
    }
}

.product-modification {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 40px;
    @media(max-width: 1600px) {
        grid-template-columns: repeat(3, 1fr);
       
    }
    @media(max-width: 1024px) {
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
    }
    @include xs-block {
        grid-template-columns: 1fr;
    }
    &__item {
        padding: 30px;
        background-color: #F5F5F5;
        border-radius: 8px;
        min-height: 290px;
        @media(max-width: 1024px) {
            min-height: 220px;
        }
        @include sm-block {
            min-height: 190px;
        }
    }
    &__subtitle {
        margin-bottom: 33px;
        font-weight: 500;
        @include sm-block {
            font-size: 16px;
            line-height: 1;
            margin-bottom: 25px;
        }
    }
    &__radio {
        margin-bottom: 24px;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include sm-block {
            margin-bottom: 20px;
        }
    }
    &__list {
        overflow-y: auto;
        height: 179px;
        &::-webkit-scrollbar {
            width: 3px;
            background-color: rgba(#1C1C1C, .05);
        }
        &::-webkit-scrollbar-thumb {
            width: 3px;
            background-color: rgba(#1C1C1C, .3);
        }
        @media(max-width: 1024px) {
            height: 131px;
        }
        @include sm-block {
            height: 92px;
        }
    }
    &__action {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        gap: 30px;
        button {
            max-width: 100%;
            width: 100%;
        }
    }
}

.product-tabs {
    &__content {
        padding: 50px 0 90px;
    }
    &-head {
        display: flex;
        align-items: center;
    }
    &__item {
        width: 50%;
        flex: 50% 0 0;
        text-align: center;
        font-size: 24px;
        line-height: 1;
        color: #8A8A8A;
        font-weight: 500;
        text-align: center;
        padding: 20px;
        cursor: pointer;
        border-bottom: 2px solid rgba(0, 0, 0, 0.1);
        &.active {
            color:#03A543;
            border-bottom: 2px solid #03A543;
        }
    }
}

.product-banner {
    background-color: $color-black;
    &__content {
        display: flex;
        align-items: center;
    }
    &__info {
        max-width: 510px;
        width: 100%;
    }
    &__img {
        width: 100%;
        img {
            display: block;
            width: 100%;
        }
    }
    &__title {
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 70px;
        font-weight: 500;
        margin-bottom: 30px;
        color: #fff;
    }
    &__desc {
        margin-bottom: 158px;
        color: #fff;
        font-size: 20px;
        line-height: 28px;
    }
    &__btn {
        max-width: 430px;
        width: 100%;
    }
}